d;\r\n  color: #0000ff;\r\n  }\r\n\r\n/* Absatz fr URL usw. */\r\n\r\np.Ende {\r\n  font-size: 10pt;                               /* Schriftgre */\r\n  }\r\n\r\n/*-------------------------------------------------------------------------------------------------\r\n  Vorgaben fr Apps\r\n  -------------------------------------------------------------------------------------------------*/\r\n\r\n/* Gesamte App */\r\n\r\ndiv#total {\r\n  position: relative;\r\n  margin-top: 20px;                              /* Abstand nach oben */\r\n  margin-left: auto;\r\n  margin-right: auto;         /* Waagrechte Zentrierung */\r\n  }\r\n\r\n/* Zeichenflche */\r\n\r\ndiv#canvas {\r\n  position: absolute;\r\n  background-color: #ffff00;                     /* Hintergrundfarbe */\r\n  border: 1px solid #000000;                     /* Rand */\r\n  }\r\n\r\n/* Schaltflche */\r\n\r\ndiv#panel, div#panel1, div#panel2 {\r\n  position: absolute;\r\n  background-color: #00ff00;                     /* Hintergrundfarbe */\r\n  border: 1px solid #000000;                     /* Rand */\r\n  font-size: 12px;                               /* Schriftgre */\r\n  font-weight: bold;                             /* Fettdruck */\r\n  }\r\n\r\n/* Zeile einer Schaltflche */\r\n\r\ndiv#panel div , div#panel1 div, div#panel2 div {\r\n  position: absolute;\r\n  }\r\n\r\n/* Inline-Element einer Schaltflche */\r\n\r\ndiv#panel span, div#panel1 span, div#panel2 span {\r\n  position: absolute;\r\n  white-space: nowrap;                           /* Kein Zeilenumbruch bei Leerzeichen */\r\n  font-family: sans-serif;\r\n  font-size: 12px;\r\n  }\r\n\r\n/* Schaltknopf */\r\n\r\nbutton {\r\n  height: 30px;                                  /* Hhe */\r\n  border-radius: 0px;                            /* Abgerundete Ecken verhindern */\r\n  margin-left: 10px;                             /* Abstand vom linken Rand */\r\n  font-family: sans-serif;                       /* Schriftart */\r\n  font-size: 12px;              